        <div class="flex-row-center flex-1 flex-start">
          <div class="swap-info PB-20 imputation">
            <div class="dor swap-item">
                <div class="flex-row-between text-16">
                    可领取DOR数量：{{getBit(utils.formatUnits(availableNum, 18), 5) }}
                    <el-icon class="is-loading" v-if="loading">
                        <Loading />
                    </el-icon>
                </div>
                <div class="flex-row-between text-16">
                    价值:~${{getBit(utils.formatUnits(availableUsdt, 18),5) }}
                    <el-icon class="is-loading" v-if="loading">
                        <Loading />
                    </el-icon>
                </div>
                <div class="MT-10 flex-row-between">
                <el-button class="dor-btn" :disabled="availableNum <= 0" v-if="isLogin" :loading="rewardLoading" @click="availableHandler">领取</el-button>
                <el-button class="dor-btn" v-else @click="setConnectDialog(true)">链接钱包</el-button>
                <a href="/#/swap/Pancake?inputCurrency=0x23766cb8a96ff2f46f664bc7d088a6306de73618&chart=1" target="_bank" class="dor-btn flex-column-center align-center buy pointer text-center">
                    <span>Swap</span>
                </a>
            </div>
            </div>
          </div>
        </div>
        
      </el-tab-pane>
      <el-tab-pane label="AMO原始股分红" name="AMO" lazy>
        <div class="flex-row-center flex-1 flex-start">
          <div class="swap-info PB-20 imputation">
            <div class="dor swap-item">
                <div class="flex-row-between text-16">
                    不建议购买AMO
                </div>
                <div class="flex-row-between text-16">
                    可领取AMO数量：{{getBit(utils.formatUnits(AmoNum, 18), 5) }}
                    <el-icon class="is-loading" v-if="loading">
                        <Loading />
                    </el-icon>
                </div>
                <div class="flex-row-between text-16">
                    价值:~${{getBit(utils.formatUnits(AmoUsdt, 18),5) }}
                    <el-icon class="is-loading" v-if="loading">
                        <Loading />
                    </el-icon>
                </div>
                <div class="MT-10 flex-row-between">
                <el-button class="dor-btn" :disabled="AmoNum <= 0" v-if="isLogin" :loading="rewardLoading" @click="getReward">领取</el-button>
                <el-button class="dor-btn" v-else @click="setConnectDialog(true)">链接钱包</el-button>
                <a href="/#/swap/Pancake?inputCurrency=0x7a349cccbB341D57e274c298f1e4751fB33158E5&chart=1" target="_bank" class="dor-btn flex-column-center align-center buy pointer text-center">
                    <span>Swap</span>
                </a>
            </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="其他说明" name="other" lazy>
        <div class="flex-row-center flex-1 flex-start">
          <div class="swap-info PB-20 imputation">
            <div class="dor swap-item">
                <div class="flex-row-between text-16">
                  本页面是提供给受伤用户继续提取链上资产,并非与本站有关!
                    <br/>
                    有其他项目奖励领取可以联系管理员
                </div>
                <a href="https://t.me/TeaDaoCn">https://t.me/TeaDaoCn</a>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
  
  
</template>

<script>
import { mapActions } from 'vuex'
import { product } from '@/plugins/wallet/config.json'
import DOR_ABI from '@/api/abis/dor.json'

import AMO_ABI from '@/api/abis/amo.json'
import { utils } from 'ethers'
import { getBit, Tips } from '../../libs/utils'
import { ElNotification } from 'element-plus'

let USDT_ADDRESS = '0x55d398326f99059fF775485246999027B3197955'
export default {
    data() {
        return {
            utils,
            getBit,
            isLogin: false,
            availableNum: 0,
            availableUsdt: 0,
            activeName: 'imputation',
            contract: null,
            AmoContract:null,
            AmoNum:0,
            AmoUsdt:0,
            AmorewardLoading:false,
            Aloading:false,
            loading: false,
            rewardLoading: false,
            
        }
    },
    mounted() {
        this.isLogin = window.wallet.accounts[0] ? true : false
        this.isLogin && this.loginCallBack()
        window.wallet.on('accountChange', () => {
            this.isLogin = window.wallet.accounts[0] ? true : false
            this.isLogin && this.loginCallBack()
        })
    },
    methods: {
        ...mapActions(['setConnectDialog']),
        loginCallBack() {
            this.contract = window.wallet.initContract(product.DOR, DOR_ABI)
            this.AmoContract = window.wallet.initContract('0xea4fb5ad31602a5a02fe7e16fbfdc2c23702fa76', AMO_ABI)
            console.log(this.contract)
            this.loading = true
            this.contract.available('0x23766CB8a96fF2f46F664BC7D088a6306DE73618',window.wallet.accounts[0]).then(data => {
                this.availableNum = data
                 window.wallet.ContractWETH.getAmountsOut(data, [
                  '0x23766CB8a96fF2f46F664BC7D088a6306DE73618',
                  USDT_ADDRESS
                ]).then(data1 => {
                  this.availableUsdt = data1[1];
                }) ;
            }).finally(() => {
                this.loading = false
            })

            this.Aloading = true
            this.AmoContract.earned(window.wallet.accounts[0]).then(data => {
                this.AmoNum = data
                 window.wallet.ContractWETH.getAmountsOut(data, [
                  '0x7a349cccbB341D57e274c298f1e4751fB33158E5',
                  USDT_ADDRESS
                ]).then(data1 => {
                  this.AmoUsdt = data1[1];
                }) ;
            }).finally(() => {
                this.Aloading = false
            })

        },// 领取奖励
        async getReward() {
            this.AmorewardLoading = true
            try {
                const tx = await this.AmoContract.getReward()
                await tx.wait()
                ElNotification({
                    type: 'success',
                    title: 'Success',
                    message: '代币领取成功'
                })   
            } catch (error) {
              Tips(error)  
            }   
            this.AmorewardLoading = false
        },
        // 领取奖励
        async availableHandler() {
            this.rewardLoading = true
            try {
                const tx = await this.contract.claim('0x23766CB8a96fF2f46F664BC7D088a6306DE73618')
                await tx.wait()
                ElNotification({
                    type: 'success',
                    title: 'Success',
                    message: '代币领取成功'
                })   
            } catch (error) {
              Tips(error)  
            }   
            this.rewardLoading = false
        }
    }
}
</script>


